body,
html {
  font-family: Arial;
  
  background-image: url("../images/TTBWallpaper.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.header img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
}

.links {
  text-align: center;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  font-weight: bold;

  list-style-type: "";
  font-size: 20px;
  max-width: 1000px;
  color: black;
  text-decoration: none;
}

.links a {
  text-decoration: none;
  display: inline-block;
  padding: 5px;
}

.dragonball {
  height: 25px;
  width: 25px;
  margin-right: 5px;
  vertical-align: middle;
  margin-bottom: 4px;
}

.list {
  line-height: 2;
}

.maincontainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.centercontainer {
  max-width: 1000px;
  display: inline-block;
}

.footer {
  margin-top: 50px;
  font-style: italic;
  font-size: 12px;
  text-align: center;
  color: white;
}

@media only screen and (max-width: 999px) {

  .links {
    margin-right: 4px;
    padding: 2px;
  }

  .dragonball {
    height: 20px;
    width: 20px;
    margin-right: 5px;
  }
  .maincontainer {
    width: 100% !important;
  }
  .centercontainer {
    width: 100%;
  }
  .header img {
    width: 20;
  }
}

@media(max-width: 767px){
  body,html {
    background-attachment: fixed;
    background-size: initial;
  }
}